.body {
    position: relative;
    width: 100vw;
}

/* not very reactive */
.home-page-content {
    padding-bottom: 60px;
}

.slide-component {
    opacity: 0;
    animation-name: slideAnimation;
    animation-timing-function: ease-in-out;
    animation-duration: 0.5s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-delay: 150ms;
    animation-fill-mode: forwards;
    text-align: left;
}

.technical-skill-icon {
    max-width: 6rem;
    max-height: 6rem;
}

@keyframes slideAnimation {
    0% {
        opacity: 0%;
        transform: translateY(5%);
    }

    100% {
        opacity: 100%;
        transform: translateY(0%);
    }
}

.Title {
    padding: 0;
    margin: 0;
    font-size: 2.5rem;
    /* color: #a3b5d6; */
}

.my-name {
    color: #E59500;
} 

.tech-skills {
    display: grid;
    justify-content: space-between;
    justify-items: left;
    grid-column-gap: 5rem;
    grid-template-columns: repeat(auto-fit, minmax(20px, 1fr));
    margin-right: 10%;
}

.grid-item {
    display: inline-grid;
}


.grid-icon {
    display: inline-grid;
    margin: 10px;
    transition: 0.5s;
}

.grid-icon:hover {
    transform: scale(1.5);
    transition: 0.5s;
}
